import React, {useContext} from 'react';
import {Icon} from '@iconify/react';
import {Link} from "react-router-dom";
import {InternetContext} from "../../context";
import "./praduktlist.scss"
import styled from "styled-components";



const PraductCom = () => {
    const {dataItems, addTocart} = useContext(InternetContext)

    return (
        <div className="tab-pane active" id="grid">
            <div className="shop-product-wrap">
                <div className="row">
                    {dataItems.map(el => {
                        return (
                            <div key={el.id} className="col-lg-4 col-md-4 col-sm-6">
                                <Div className="single-product-wrap mb-4">
                                    <div className="product-image">
                                        {/* eslint-disable-next-line jsx-a11y/anchor-is-valid */}
                                        <Link to={`/shop/${el.id}`}><img src={el.img1} alt="Produce Images"/></Link>
                                        {/*<span className="label">30% Off</span>*/}
                                        <div className="product-action">
                                            {/* eslint-disable-next-line jsx-a11y/anchor-is-valid */}
                                            <a onClick={() => addTocart(el)} className="add-to-cart">
                                                <Icon icon="ion:bag-handle-outline"/></a>
                                            {/* eslint-disable-next-line jsx-a11y/anchor-is-valid */}
                                            <a href="#" className="wishlist">
                                                <Icon icon="zmdi:favorite-outline"/></a>
                                            {/* eslint-disable-next-line jsx-a11y/anchor-is-valid */}
                                            <Link to={`/shop/${el.id}`}  className="quick-view"
                                               data-toggle="modal"
                                               data-target="#exampleModalCenter">
                                                <Icon icon="bx:bx-search-alt"/></Link>
                                        </div>
                                    </div>
                                    <div className="product-content">
                                        {/* eslint-disable-next-line jsx-a11y/anchor-is-valid */}
                                        <h3><Link to={`/shop/${el.id}`}><a href="#">{el.name}</a></Link>
                                        </h3>
                                        <div className="price-box">
                                            {/*<span className="old-price">$56</span>*/}
                                            <span className="new-price">{el.price} UZS</span>
                                        </div>
                                    </div>
                                </Div>
                            </div>
                        )
                    })}

                </div>
            </div>
        </div>
    );
};

const Div = styled.div`
  .single-product-wrap {
    margin-top: 30px;
    box-shadow: 5px 5px 11px 0 blue;
    border-radius: 3%;
  }
`

export default PraductCom;