import React from 'react';
import styled from "styled-components";

const ComentariyCom = () => {
    return (
        <Div>
            <div className="tab-pane" id="tab-add">
                <form className="form-horizontal" id="form-review">
                    <div className="form-group row required">
                        <div className="col-md-6 col-sm-12">
                            <input type="text" name="name" placeholder="Name*"
                                   id="input-name" className="form-control"/>
                        </div>
                        <div className="col-md-6 col-sm-12">
                            <input type="text" name="email" placeholder="Email*"
                                   id="input-email" className="form-control"/>
                        </div>
                    </div>
                    <div className="form-group row required">
                        <div className="col-sm-12">
                            <textarea name="text" id="input-review" placeholder="Your Reviews*"
                                      className="form-controll higt"/>
                        </div>
                    </div>

                    <div className="col-lg-12">
                        <div className="comment-form-submit">
                            <button className="comment-submit">SUBMIT</button>
                        </div>
                    </div>
                </form>
            </div>
        </Div>
    );
};
const Div = styled.div`
  #input-name, #input-email, .form-control {
    display: block;
    width: 100%;
    height: calc(1.5em + .75rem + 2px);
    padding: .375rem .75rem;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
    color: #495057;
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid #ced4da;
    border-radius: .25rem;
    transition: border-color .15s ease-in-out, box-shadow .15s ease-in-out;
  }

  #input-review .form-control {
    min-height: 305px;
    box-shadow: none;
    margin: 0 0 30px;
    resize: none;
    height: 350px;
  }

  .comment-form-submit {
    margin-top: 10px;
  }

  .comment-form-submit .comment-submit {
    background: #ff5151;
    color: #ffffff;
    font-weight: 600;
    border: none;
    text-transform: uppercase;
    padding: 8px 30px;
    font-size: 16px;
    letter-spacing: 1px;
    border-radius: 5px;
  }

  .comment-form-submit .comment-submit:hover {
    background: #333;
  }


`
export default ComentariyCom;