import React, { useState,  } from 'react';
import styled from "styled-components";
import { Link } from "react-router-dom";
import "./style.scss"


const RegisterStartCom = () => {
    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");
    const [username, setUsername] = useState("");
    const [exit, setExit] = useState("");

    async function registration(event) {
        event.preventDefault();
        const response = await fetch("http://81.95.232.205:8007/register/", {
            method: "POST",
            headers: {
                "Content-Type": "application/json",
            },
            body: JSON.stringify({
                username: username,
                email: email,
                password: password,
            }),
        });
        const data = await response.json();
        window.location = "/login";

        console.log(data);
        setExit(data);
        console.log(exit)
    }


    return (
        <Div>
            <div className="login">
                <div className="container">
                    <div className="row justify-content-center">

                        <div className="col-sm-12 col-md-6">
                            <div className="row">
                                <div className="modal-content">
                                    <div className="modal-body">
                                        <h1 className="me-login-title">Sign Up</h1>
                                        <form>
                                            <div className="me-login-form">
                                                <input className="form-control" type="text" value={username} name={username}
                                                       onChange={((e) => setUsername(e.target.value))}
                                                       placeholder="Username" required
                                                />
                                                <input className="form-control" type="text" value={email} name={email}
                                                       onChange={((e) => setEmail(e.target.value))}
                                                       placeholder="Email" required
                                                />
                                                <input className="form-control" type="password" value={password} name={password}
                                                       onChange={((e) => setPassword(e.target.value))}
                                                       placeholder="Password" required
                                                />

                                                <div className="me-login-btn">
                                                    <button onClick={registration} className="me-btn">Sign up</button>
                                                    <p>Already have an account?
                                                        <Link to="/login" data-toggle="modal" data-target="#meLogin"
                                                              data-dismiss="modal" aria-label="Close">Login</Link>
                                                    </p>
                                                </div>
                                            </div>
                                        </form>


                                    </div>
                                </div>

                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </Div>
    );
};
const Div = styled.div`
  margin: 8% 0 ;
  .form-control {
    display: block;
    width: 100%;
    height: calc(1.5em + .75rem + 2px);
    padding: .375rem .75rem;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
    color: #495057;
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid #ced4da;
    border-radius: .25rem;
    transition: border-color .15s ease-in-out, box-shadow .15s ease-in-out;
  }

  .loginnow {
    height: 615px;
  }
`



export default RegisterStartCom;