import React, {useContext,} from 'react';
import {InternetContext} from "../../context";
import {useParams} from 'react-router'
import "../ShopDetalsCom/style.scss"
import "./swaipr.css"
import "./stayle.scss"
import SwaiperCom from "./SwaiperCom";
import {Icon} from '@iconify/react';

const ZakazIdCom = () => {

    const {dataItems, addTocart} = useContext(InternetContext)
    const params = useParams()
    console.log(params)
    console.log(dataItems)

    const data = dataItems.find(el => el.id == params.id)
    console.log(data)

    return (
        <div className="row">
            <div key={data.id} className="col-sm-5 col-12">
                <div>
                    <SwaiperCom data={data}/>
                </div>
            </div>
            <div className="col-sm-7 col-12">
                <div className="col-xl-6 col-lg-5 col-md-6">
                    <div className="product_dextails_info ">
                        <h2>{data.name}</h2>
                        <div className="pro_rating d-flex ">
                            {/*<div className="rating">*/}
                            {/*    <i className={data.rating >= 1 ? 'fas fa-star yellow' : data.rating >= 0.5 && data.rating < 1 ? 'fas fa-star-half white' : 'far fa-star white'}/>*/}
                            {/*    <i className={data.rating >= 2 ? 'fas fa-star yellow' : data.rating >= 1.5 && data.rating < 2 ? 'fas fa-star-half white' : 'far fa-star white'}/>*/}
                            {/*    <i className={data.rating >= 3 ? 'fas fa-star yellow' : data.rating >= 2.5 && data.rating < 3 ? 'fas fa-star-half white' : 'far fa-star white'}/>*/}
                            {/*    <i className={data.rating >= 4 ? 'fas fa-star yellow' : data.rating >= 3.5 && data.rating < 4 ? 'fas fa-star-half white' : 'far fa-star white'}/>*/}
                            {/*    <i className={data.rating >= 5 ? 'fas fa-star yellow' : data.rating >= 4.5 && data.rating < 5 ? 'fas fa-star-half white' : 'far fa-star white'}/>*/}
                            {/*</div>*/}
                            <span className="rat_qun">  </span>
                        </div>

                        <div className="pro_details">
                            <p>Lorem ipsum dolor sit amet consectetur adipisicing elit, sed do eiusmod temf incididunt
                                ut labore et dolore magna aliqua. Ut enim ad minim veniam, nostr exercitation ullamco
                                laboris nisi ut aliquip ex ea. </p>
                        </div>
                        <h2 className="title_2">Narxi </h2>
                        <ul className="pro_dtl_prize ">
                            <li className="text-start">{data.price} UZS</li>
                        </ul>
                        <ul className="text-start d-flex">
                            <li><b>Razmer : </b> </li>
                            <li className="mx-4"> {data.size} </li>
                        </ul>

                        <div className="product-quantity-action">
                            <div className="prodict-statas"><span>Quantity :</span></div>
                            <div className="product-quantity">
                                <form action="#">
                                    <div className="product-quantity">
                                        <div className="cart-plus-minus">
                                            <input placeholder="1" type="number"/>
                                        </div>
                                    </div>
                                </form>
                            </div>
                        </div>

                        <ul className="pro_dtl_btn">
                            {/* eslint-disable-next-line jsx-a11y/anchor-is-valid */}
                            <li onClick={() => addTocart()}><a href="#" className="buy_now_btn">buy now</a></li>
                            {/* eslint-disable-next-line jsx-a11y/anchor-is-valid */}
                            <li><a href="#"><Icon icon="ion:heart"/></a></li>
                        </ul>

                        <div className="pro_social_share d-flex">
                            <h2 className="title_2">Share :</h2>
                            <ul className="pro_social_link">
                                {/* eslint-disable-next-line jsx-a11y/anchor-is-valid */}
                                <li><a href="#"><Icon icon="akar-icons:instagram-fill"/></a></li>
                                {/* eslint-disable-next-line jsx-a11y/anchor-is-valid */}
                                <li><a href="#"><Icon icon="akar-icons:facebook-fill"/></a></li>
                                {/* eslint-disable-next-line jsx-a11y/anchor-is-valid */}
                                <li><a href="#"><Icon icon="akar-icons:telegram-fill"/></a></li>
                                {/* eslint-disable-next-line jsx-a11y/anchor-is-valid */}
                                <li><a href="#"><Icon icon="bx:bxl-tiktok"/></a></li>
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default ZakazIdCom;