import React from 'react';
import InputlogoCom from "./InputlogoCom";
import RegisrationCom from "./RegisrationCom";
import YourOrderCom from "./YourOrderCom";
import "./style.scss"

const CheckoutCom = () => {
    return (
        <div>
            <div className="main-content-wrap section-ptb checkout-page">
                <div className="container">
                    <div className="row">
                        <div className="col">
                            <div className="coupon-area">
                                <InputlogoCom />
                            </div>
                        </div>
                    </div>
                    <div className="checkout-details-wrapper">
                        <div className="row">
                            <RegisrationCom/>
                            <YourOrderCom />
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default CheckoutCom;