import React from 'react';
import {Carousel} from "react-bootstrap";
import png1 from "../../img/N1.png"
import png2 from "../../img/N2.png"
import "./style.scss"

const CarouselCom = () => {
    return (
        <Carousel  className="CarouselCom">
            <Carousel.Item  interval={2000}>
                <img
                    className="d-block w-100 carousel_img1"
                    src={png1}
                    alt="First slide"
                />
                <Carousel.Caption className="carousel_div">
                    <h3 className="carousel_h3">Ayollarning Ko'rki
                        bu <b>ROMOL</b></h3>
                    <p className="carousel_p">eng chroyli romollar faqat bizda</p>
                </Carousel.Caption>
            </Carousel.Item>
            <Carousel.Item>
                <img
                    className="d-block w-100"
                    src={png2}
                    alt="Second slide"
                />

                <Carousel.Caption className="carousel_div1">
                    <h3 className="carousel_h3">Ayollarning Ko'rki
                        bu <b>ROMOL</b></h3>
                    <p className="carousel_p">eng chroyli romollar faqat bizda</p>
                </Carousel.Caption>
            </Carousel.Item>
        </Carousel>
    );
};

export default CarouselCom;