import React, {useContext} from 'react';
import ZakazIdCom from "../ZakazIDCom/ZakazIDCom";
import JonhDouCom from "../JonhDouCom/JonhDouCom";
import ComentariyCom from "../JonhDouCom/ComentariyCom";
import {InternetContext} from "../../context";
import "./style.scss"
import MenCardCom from "../ZakazIDCom/MenCardCom";
import "../ZakazIDCom/stayle.scss"

const ShopDetalsCom = () => {
    const {shopCard, setShopCard,} = useContext(InternetContext)
    return (
        <div>
            <div className="shop">
                <div className="container">
                    <div className="row">
                        <div className=" mainpage">
                            <div className="row shopdetail">
                                     <ZakazIdCom />

                                <div className="col-sm-12 col-12 text-start">
                                    <ul className="nav nav-tabs active">
                                        <li onClick={() => {
                                            setShopCard('coment')
                                        }} className="nav-item">
                                            {/* eslint-disable-next-line jsx-a11y/anchor-is-valid */}
                                            <a className="nav-link" data-toggle="tab">izohlar</a>
                                        </li>
                                        <li onClick={() => {
                                            setShopCard('input')
                                        }} className="nav-item">
                                            {/* eslint-disable-next-line jsx-a11y/anchor-is-valid */}
                                            <a className="nav-link" data-toggle="tab">izoh qoldrish</a>
                                        </li>
                                    </ul>

                                        {shopCard === 'coment' && <JonhDouCom/>}
                                        {shopCard === 'input' && <ComentariyCom/>}
                                </div>
                            </div>

                                <MenCardCom/>

                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default ShopDetalsCom;