import React, {useContext} from 'react';
import {InternetContext} from "../../context";
import {Icon} from "@iconify/react";
import {Link} from "react-router-dom";

const MenCardCom = () => {
    const {dataItems, addTocart} = useContext(InternetContext)
    return (
        <div>
            <div className="row">
                <div className="col-sm-12 commontop text-center">
                    <div className="row">
                        <div className="col-lg-12">
                            <div className="section-title text-center">
                                <h2><span>Biznig</span> Mahsulotlar</h2>
                                <p>There are many variations of passages of Lorem Ipsum available, but the majority have
                                    suffered lebmid alteration in some ledmid form</p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div className="row">
                {dataItems.slice(0, 4).map(el => {
                    return (
                        <div key={el.id} className="col-lg-3 col-md-3 col-sm-6 mb-5">
                            <div className="single-product-wrap">
                                <div className="product-image">
                                    {/* eslint-disable-next-line jsx-a11y/anchor-is-valid */}
                                    <a href="#"><img src={el.img1} alt="Produce Images"/></a>
                                    {/*<span className="label">30% Off</span>*/}
                                    <div className="product-action">
                                        {/* eslint-disable-next-line jsx-a11y/anchor-is-valid */}
                                        <a onClick={() => addTocart(el)} href="#" className="add-to-cart">
                                            <Icon icon="ion:bag-handle-outline"/></a>
                                        {/* eslint-disable-next-line jsx-a11y/anchor-is-valid */}
                                        <a href="#" className="wishlist">
                                            <Icon icon="zmdi:favorite-outline"/></a>
                                        {/* eslint-disable-next-line jsx-a11y/anchor-is-valid */}
                                        <Link to={`/shop/${el.id}`} className="quick-view">
                                            <Icon icon="bx:bx-search-alt"/></Link>
                                    </div>
                                </div>
                                <div className="product-content">
                                    <h3><Link to={`/shop/${el.id}`}>
                                        {/* eslint-disable-next-line jsx-a11y/anchor-is-valid */}
                                        <a href="#" >{el.name}</a></Link>
                                    </h3>
                                    <div className="price-box">
                                        {/*<span className="old-price">$56</span>*/}
                                        <span className="new-price">{el.price} UZS</span>
                                    </div>
                                </div>
                            </div>
                        </div>
                    )
                })}

            </div>
        </div>
    );
};

export default MenCardCom;