import React, {useContext} from 'react';
import "./stayle.scss"
import {Icon} from '@iconify/react';
import styled from "styled-components";
import {InternetContext} from "../../context";

const ContactCo = () => {
    const {name, setName, email, setEmail, subject, setSubject, number, setNumber, sendData,} = useContext(InternetContext)



    return (
        <Div>
            <section id="contact" className="contact">
                <div className="container" data-aos="fade-up">

                    <div className="section-title">
                        <h3><span>Aloqa</span></h3>
                    </div>

                    <div className="row" data-aos="fade-up" data-aos-delay="100">
                        <div className="col-lg-6">
                            <div className="info-box mb-4">
                                <Icon icon="bx:bx-map" color="blue" width="40" height="40"/>
                                <h3>Bizning Manzil</h3>
                                <p>Toshkent </p>
                            </div>
                        </div>

                        <div className="col-lg-3 col-md-6">
                            <div className="info-box  mb-4">
                                <Icon icon="clarity:envelope-line" color="blue" width="40" height="40"/>
                                <h3>Elektron pochta</h3>
                                <p>asqarabdusamatov2@gmsil.com</p>
                            </div>
                        </div>

                        <div className="col-lg-3 col-md-6">
                            <div className="info-box  mb-4">
                                <Icon icon="bx:bx-phone-call" color="blue" width="40" height="40"/>
                                <h3>Qo'ng'iroq qiling</h3>
                                <p>+998 99 825 68 25</p>
                            </div>
                        </div>

                    </div>

                    <div className="row" data-aos="fade-up" data-aos-delay="100">

                        <div className="col-lg-6 ">
                            {/* eslint-disable-next-line jsx-a11y/iframe-has-title */}
                            <iframe
                                src="https://yandex.ru/map-widget/v1/?um=constructor%3A6f9ea356c4b0cf4d2e27ff3da63bcb893ea521050d104145d7f5bb15776ad126&amp;source=constructor"
                                width="648" height="350" frameBorder="0"/>
                        </div>

                        <div className="col-lg-6">
                            {/* eslint-disable-next-line jsx-a11y/no-redundant-roles */}
                            <form method="post" role="form" className="php-email-form">
                                <div className="row">
                                    <div className="col form-group">
                                        <input
                                            type="text"
                                            name="name"
                                            className="form-control"
                                            id="name"
                                            placeholder="Your Name"
                                            onChange={(e) => setName(e.target.value)}
                                            value={name}
                                            required/>

                                    </div>
                                    <div className="col form-group">
                                        <input
                                            type="email"
                                            className="form-control"
                                            name="email"
                                            id="email"
                                            placeholder="Your Email"
                                            required
                                            onChange={(e) => setEmail(e.target.value)}
                                            value={email}
                                        />
                                    </div>
                                </div>
                                <div className="form-group">
                                    <input
                                        type="text"
                                        className="form-control"
                                        name="address"
                                        id="subject"
                                        placeholder="Subject"
                                        onChange={(e) => setSubject(e.target.value)}
                                        value={subject}
                                        required/>
                                </div>
                                <div className="form-group">
                                    <input
                                        type="number"
                                        className="form-control"
                                        name="number"
                                        id="subject"
                                        placeholder="Number"
                                        onChange={(e) => setNumber(e.target.value)}
                                        value={number}
                                        required/>
                                </div>
                                <div className="my-3">
                                    <div className="loading">Loading</div>
                                    <div className="error-message"/>
                                    <div className="sent-message">Your message has been sent. Thank you!</div>
                                </div>
                                <div className="text-center">
                                    <button onClick={sendData} type="submit">Send Message</button>
                                </div>
                            </form>
                        </div>

                    </div>

                </div>
            </section>
        </Div>
    );
};

const Div = styled.data`
  .contact {
    margin-top: 8%;

    .section-title h3 {
      margin: 15px 0 20px 0;
      font-size: 32px;
      font-weight: 700;
      color: blue;
      text-align: center;
    }
  }


`
export default ContactCo;