import React, {useContext} from 'react';
import {Navbar, Nav,} from "react-bootstrap";
import "./navbarcom.scss"
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faSearch, faUser, faShoppingBag} from "@fortawesome/free-solid-svg-icons";
import styled from "styled-components";
import {Link} from "react-router-dom";
import logo from "../../img/logo/logo.png"
import {InternetContext} from "../../context";
import CorzinkaCom from "./CorzinkaCom";

const NavbarCom = () => {
    const {cardArray} = useContext(InternetContext)

    const data = localStorage.getItem("data");
    async function logout(event) {
        event.preventDefault();
        const response = await fetch(
            "http://81.95.232.205:8007/api/logout/",
            {
                method: "POST",
                headers: {
                    Authorization: `Token ${data}`,
                },
            }
        );
        const data2 = await response.json();

        window.location = "/";
        localStorage.clear();
        // localStorage.setItem("data", data.token);
        // console.log(data);
    }

    return (
        <div className="NavbarCom fixed-top">
            <Navbar bg="light" expand="lg">
                <div className="container">
                    <Navbar.Brand><Link to="/">
                        <img className="logo_nav" src={logo} alt=""/>
                    </Link></Navbar.Brand>
                    <NAV className="icon nav_icon1">
                        <Nav.Link className="img_logo nav_logo" href="#deets">
                            <FontAwesomeIcon className="img_logo nav_logo" icon={faSearch} size="2x"/>
                        </Nav.Link>
                        <Nav.Link className="img_logo" href="#memes">
                            <FontAwesomeIcon className="img_logo" icon={faUser} size="2x"/>
                        </Nav.Link>
                        <Nav.Link className="img_logo" href="#memes"><Link to="/cardcom">
                            <FontAwesomeIcon className="img_logo" icon={faShoppingBag} size="2x"/>
                            <sup className="supp">{cardArray.length}</sup>
                        </Link>

                        </Nav.Link>
                    </NAV>
                    <Navbar.Toggle aria-controls="navbarScroll"/>
                    <Navbar.Collapse id="navbarScroll">
                        <Nav className="navbar">
                            <ul className="d-lg-flex d-md-block text-sm-start">
                                <li className="nav_text"><Link to="/"><b>BOSH SAHIFA</b></Link></li>
                                <li className="nav_text"><Link to="/about"><b>BIZ HAQIMIZDA</b></Link></li>
                                <li className="nav_text"><Link to="/shop"><b>DOKONIMIZ</b></Link></li>
                                <li className="nav_text"><Link to="/contact"><b>ALOQA</b></Link></li>
                            </ul>
                        </Nav>
                        <NAV className="icon nav_icon">
                            {/*<Nav.Link className="img_logo nav_logo" href="#deets">*/}
                            {/*    <FontAwesomeIcon className="img_logo nav_logo" icon={faSearch} size="1x"/>*/}
                            {/*</Nav.Link>*/}
                            <li className="nav_text d-flex">
                                {data ? (
                                    <div>
                                        <Link className="btn" onClick={logout}  to="/">
                                            <b>LOGOUT</b>
                                            <CorzinkaCom/>
                                        </Link>
                                    </div>

                                ) : (
                                    <div className="d-flex">
                                        <Link className="btn "  to="/login">
                                            <b>LOGIN</b>
                                        </Link>
                                        <Link className="btn mx-4" to="/register">
                                            <b>REGISTRATION</b>
                                        </Link>
                                    </div>

                                )}
                            </li>
                        </NAV>

                    </Navbar.Collapse>
                </div>
            </Navbar>
        </div>
    );
};
const NAV = styled.nav`
  display: flex;
  justify-content: space-around;

  .supp {
    position: relative;
    width: 14px;
    height: 10px;
    margin: auto;
    top: -24px;
    left: -12px;
    padding: 4px;
    border-radius: 50%;
    background-color: red;
    color: #FFFFFF;
    text-decoration: none;
  }
  a{
    list-style: none;
    text-decoration: none;

  }
`

export default NavbarCom;