import React from "react";
import NavbarCom from "./Components/NavbarCom/NavbarCom";
import {Route, Switch} from "react-router-dom";
import HabibtiCom from "./pegs/HabibtiCom/HabibtiCom";
import ContactCo from "./pegs/ContactCo/ContactCo";
import ShopCom from "./pegs/ShopCom/ShopCom";
import ShopingCardsCom from "./Components/SohingCardCom/ShopingCardsCom";
import AboutCom from "./Components/AboutCom/AboutCom";
import FooterCom from "./Components/FooterCom/FooterCom";
import CheckoutCom from "./Components/CheckoutCom/CheckoutCom";
import CarddCom from "./Components/CarddCom/CarddCom"
import LoginCom from "./Components/LoginCom/LoginCom";
import RegisterCom from "./Components/RegisrerCom/RegisterCom";
import LoginStartCom from "./Components/LoginStartCom/LoginStartCom";
import RegisterStartCom from "./Components/LoginStartCom/RegisterStartCom";


function App() {
    return (
        <div className="App">
            <NavbarCom/>
            <Switch>
                <Route exact path="/">
                    <HabibtiCom/>
                </Route>
                <Route exact path="/home">
                    <HabibtiCom/>
                </Route>
                <Route exact path="/about">
                    <AboutCom/>
                </Route>
                <Route exact path="/shop">
                    <ShopCom/>
                </Route>
                <Route exact path="/shop/:id">
                    <ShopingCardsCom/>l
                </Route>
                <Route exact path="/cardcom">
                    <CarddCom/>
                </Route>
                <Route exact path="/checkout">
                    <CheckoutCom/>
                </Route>
                <Route exact path="/contact">
                    <ContactCo/>
                </Route>
                <Route exact path="/login">
                    <LoginStartCom/>
                </Route>
                <Route exact path="/register">
                    <RegisterStartCom/>
                </Route>
            </Switch>
            <FooterCom/>
        </div>
    );
}

export default App;
