import React, { useState} from 'react';
import styled from "styled-components";
import "./style.scss"
import {Link} from "react-router-dom";



const LoginStartCom = () => {
    const [password, setPassword] = useState("");
    const [username, setUsername] = useState("");

    async function Login(event) {
        event.preventDefault();
        const response = await fetch(
            "http://81.95.232.205:8007/api/login/",
            {
                method: "POST",
                headers: {
                    "Content-Type": "application/json",
                },
                body: JSON.stringify({
                    username: username,
                    password: password,
                }),
            }
        );
        const data = await response.json();

        window.location = "/shop";
        localStorage.setItem("data", data.token);
        console.log(data);
    }

    return (
        <Div>
            <div className="login">
                <div className="container">
                    <div className="row justify-content-center">
                        <div className="col-sm-12 commontop text-center">
                            <H4>Login to Your Account</H4>
                        </div>
                        <div className="col-sm-12 col-md-6 mb-5">
                            <div className="row">
                                <div className="modal-content">
                                    <div className="modal-body ">
                                        <h1 className="me-login-title">Login</h1>
                                        <form>
                                            <div className="me-login-form">
                                                <input className="form-control" type="text" placeholder="Email"
                                                       value={username} name={username}
                                                       onChange={((e) => setUsername(e.target.value))} required/>
                                                <input className="form-control" type="password" value={password}
                                                       name={password} placeholder="Password"
                                                       onChange={((e) => setPassword(e.target.value))} required/>
                                                <div className="me-remember">
                                                    <label>Remember Me
                                                        <input type="checkbox"/>
                                                        <span className="s_checkbox"/>
                                                    </label>

                                                    <Link to="/password" className="me-forgot-password" data-toggle="modal"
                                                          data-target="#meforgot" data-dismiss="modal" aria-label="Close">
                                                        Forgot Password ?
                                                    </Link>
                                                </div>
                                                <div className="me-login-btn">
                                                    <button onClick={Login} className="me-btn">Login</button>
                                                    <p>Don't have an account? <Link to="/register" data-toggle="modal"
                                                       data-target="#meSignup" data-dismiss="modal" aria-label="Close">Sign
                                                        up</Link>
                                                    </p>
                                                </div>
                                            </div>
                                        </form>

                                    </div>
                                </div>

                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </Div>
    );
};
const Div = styled.div`
  margin-top: 5%;

  .form-control {
    display: block;
    width: 100%;
    height: calc(1.5em + .75rem + 2px);
    padding: .375rem .75rem;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
    color: #495057;
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid #ced4da;
    border-radius: .25rem;
    transition: border-color .15s ease-in-out, box-shadow .15s ease-in-out;
  }
`

const H4 = styled.h4`
  margin-top: 3%;
  font-size: 36px;
  font-weight: 400;
  color: #333;
  text-transform: capitalize;
  text-shadow: 1px 1px 1px rgb(150 150 150 / 90%);

`
export default LoginStartCom