import React, {useState} from 'react';


const InputlogoCom = () => {





    return (
        <div>
            <div className="coupon-accordion">
                <h3>Qaytgan mijoz?
                    <span className="coupon" id="showlogin">Kirish uchun bu erni bosing</span>
                </h3>
                <div className="coupon-content" id="checkout-login">
                    <div className="content show content">
                        <p>Agar siz ilgari biz bilan xarid qilgan bo'lsangiz, iltimos, quyidagi maydonlarga o'z
                            ma'lumotlaringizni kiriting.
                            Agar siz yangi mijoz bo'lsangiz, billing Yetkazib berish bo'limi.</p>
                        <form>
                            <p className="coupon-input form-row-first">
                                <label>Foydalanuvchi nomi yoki elektron pochta<span
                                    className="required">*</span></label>
                                <input type="text" name="email"/>
                            </p>
                            <p className="coupon-input form-row-last">
                                <label>parol<span className="required">*</span></label>
                                <input type="password" name="password"/>
                            </p>
                            <p>
                                <button type="submit" className="button-login btn" name="login" value="Login">Kirish
                                </button>
                                <label className="remember"><input type="checkbox" value="1"/><span>Eslab qoling</span></label>
                            </p>
                            <p className="lost-password">
                                {/* eslint-disable-next-line jsx-a11y/anchor-is-valid */}
                                <a href="#">Parolni unutdingizmi?</a>
                            </p>
                        </form>
                    </div>
                </div>
            </div>

            <div className="coupon-accordion">
                <h3>Kupon bormi? <span className="coupon" id="showcoupon">Kodni kiritish uchun shu erni bosing</span>
                </h3>
                <div className="coupon-content" id="checkout-coupon">
                    <div className="coupon-info">
                        <form action="#">
                            <p className="checkout-coupon">
                                <input type="text" placeholder="Kupon kodi"/>
                                <button type="submit" className="btn button-apply-coupon">Kuponni qo'llang</button>
                            </p>
                        </form>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default InputlogoCom;