import React, {useContext} from 'react';
import FilterTopCom from "../ShopFilterCom/FilterTopCom";
import PraductCom from "../PraductCom/PraductCom";
import ShopListCom from "../PraductCom/ShopListCom";
import PaginationStartCom from "../ShopFilterCom/PaginationStartCom";
import {InternetContext} from "../../context";

const ShopFull = () => {
    const {filter, dataItems} = useContext(InternetContext)
    return (
        <div className="col-lg-12  order-1">
            <div className="shop-product-wrapper mb-5">
                <FilterTopCom/>
                <div className="shop-products-wrap">
                    <div className="tab-content">

                        <div className="tab-pane active" id="grid">

                            {filter === 'grid' && <PraductCom/>}

                        </div>

                        {dataItems.map(el => {
                            return (
                                <div key={el.id}>
                                    {filter === 'list' && <ShopListCom el={el}/>}
                                </div>
                            )
                        })}

                    </div>
                </div>
            </div>
            <PaginationStartCom/>
        </div>
    );
};

export default ShopFull;