import React, { useState} from "react";

import {Swiper, SwiperSlide} from "swiper/react";

import "./swaipr.css";
import "swiper/swiper.scss"



// import Swiper core and required modules
import SwiperCore, {
    Navigation, Thumbs
} from 'swiper';

// install Swiper modules
SwiperCore.use([Navigation, Thumbs]);


export default function SwaiperCom({data}) {


    const [thumbsSwiper, setThumbsSwiper] = useState(null);


    return (
        <div className="swaiper_div">
            <Swiper style={{'--swiper-navigation-color': '#fff', '--swiper-pagination-color': '#fff'}} spaceBetween={1}
                    navigation={true} thumbs={{swiper: thumbsSwiper}} className="mySwiper2">
                <SwiperSlide><img src={data.img1} alt={data.img1}/></SwiperSlide>
                <SwiperSlide><img src={data.img2} alt={data.img2}/></SwiperSlide>
                <SwiperSlide><img src={data.img3} alt={data.img3}/></SwiperSlide>
                <SwiperSlide><img src={data.img4} alt={data.img4}/></SwiperSlide>
            </Swiper>
            <Swiper onSwiper={setThumbsSwiper} spaceBetween={10} slidesPerView={4} freeMode={true}
                    watchSlidesProgress={true} className="mySwiper mt-3">
                <SwiperSlide><img src={data.img1} alt={data.img1}/></SwiperSlide>
                <SwiperSlide><img src={data.img2} alt={data.img2}/></SwiperSlide>
                <SwiperSlide><img src={data.img3} alt={data.img3}/></SwiperSlide>
                <SwiperSlide><img src={data.img4} alt={data.img4}/></SwiperSlide>
            </Swiper>
        </div>
    )
}