import React, {useContext} from 'react';
import {Link} from "react-router-dom";
import {Icon} from '@iconify/react';
import "./cardcom.scss"
import {InternetContext} from "../../context";

const CarddCom = () => {
    const {cardArray,  RemoveCard} = useContext(InternetContext)
    return (
        <div>
            <div className="main-content-wrap section-ptb cart-page mt-5">
                <div className="container">
                    <div className="row">
                        <div className="col-12">
                            <form action="#" className="cart-table">
                                <div className="table-content table-responsive">
                                    <table className="table">
                                        <thead>
                                        <tr>
                                            <th className="plantmore-product-thumbnail">Rasmlar</th>
                                            <th className="cart-product-name">Mahsulot</th>
                                            <th className="plantmore-product-price">Donasining narxi </th>
                                            <th className="plantmore-product-quantity">Miqdor</th>
                                            <th className="plantmore-product-subtotal">Jami</th>
                                            <th className="plantmore-product-remove">O'chirish</th>
                                        </tr>
                                        </thead>
                                        <tbody>
                                        {cardArray.map(el =>{
                                            return(
                                                <tr key={el.id}>
                                                    <td className="plantmore-product-thumbnail"><a href="#">
                                                        <img src={el.img1} alt={el.img1}/></a></td>
                                                    <td className="plantmore-product-name"><Link to={`/shop/${el.id}`}>{el.name}</Link>
                                                    </td>
                                                    <td className="plantmore-product-price"><span
                                                        className="amount">{el.price} UZS</span></td>
                                                    <td className="plantmore-product-quantity">
                                                        <input placeholder="1" type="number"/>
                                                    </td>
                                                    <td className="product-subtotal"><span className="amount">$70.00</span></td>
                                                    <td className="plantmore-product-remove"><a href="#">
                                                        <Icon onClick={() => {RemoveCard(el.id)}} icon="ion:close"/></a></td>
                                                </tr>
                                            )
                                        })}

                                        </tbody>
                                    </table>
                                </div>
                                <div className="row">
                                    <div className="col-md-8">
                                        <div className="coupon-all">

                                            <div className="coupon2">
                                                <input className="submit btn" name="savatni yangilash" value="Update cart"
                                                       type="submit"/>
                                                <Link to="/shop" className="btn continue-btn">Xarid qilishni davom ettiring</Link>
                                            </div>

                                            <div className="coupon">
                                                <h3>Kupon</h3>
                                                <p>Agar sizda bo'lsa, kupon kodini kiriting.</p>
                                                <input id="coupon_code" className="input-text" name="coupon_code"
                                                       value="" placeholder="Kupon kodi" type="text"/>
                                                <input className="button" name="amal_kupon" value="Apply coupon"
                                                       type="submit"/>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="col-md-4 ml-auto">
                                        <div className="cart-page-total">
                                            <h2>Arava jami</h2>
                                            <ul>
                                                <li>Jami <span>$170.00</span></li>
                                                <li>Jami <span>$170.00</span></li>
                                            </ul>
                                            <Link to="/checkout" className="proceed-checkout-btn">Ro'yxatdan o'tishni davom etish</Link>
                                        </div>
                                    </div>
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        </div>
);
};

export default CarddCom;