import React, {useContext} from 'react';
import {InternetContext} from "../../context"
import {Icon} from '@iconify/react';
import "./pagnetion.scss"


const PaginationStartCom = () => {
    const { nextPage, createButton} = useContext(InternetContext)
    return (
        <div>
            <div className="paginatoin-area">
                <div className="row">
                    <ul className="pagination-box">
                        {/* eslint-disable-next-line jsx-a11y/anchor-is-valid */}
                        <li><a  href="#" className="Previous text-danger">
                            <Icon icon="el:chevron-left" />
                        </a>
                        </li>

                        {createButton.map((el, index) => {
                            return(
                                <li className="page-item ">
                                    <a className='page-link' onClick={() => nextPage(index)}>{index + 1}</a>
                                </li>
                            )
                        })}
                        <li>
                            {/* eslint-disable-next-line jsx-a11y/anchor-is-valid */}
                            <a href="#" className="Next btn-outline-danger"  >
                                <Icon icon="el:chevron-right" />
                            </a>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
    );
};

export default PaginationStartCom;