import React from 'react';

const YourOrderCom = () => {
    return (
        <div className="col-lg-6 col-md-6">
            <div className="your-order-wrapper">
                <h3 className="shoping-checkboxt-title">Your Order</h3>
                <div className="your-order-wrap">
                    <div className="your-order-table table-responsive">
                        <table>
                            <thead>
                            <tr>
                                <th className="product-name">Product</th>
                                <th className="product-total">Total</th>
                            </tr>
                            </thead>
                            <tbody>
                            <tr className="cart_item">
                                <td className="product-name">
                                    Vestibulum suscipit <strong className="product-quantity"> ×
                                    1</strong>
                                </td>
                                <td className="product-total">
                                    <span className="amount">£165.00</span>
                                </td>
                            </tr>
                            <tr className="cart_item">
                                <td className="product-name">
                                    Vestibulum magna <strong className="product-quantity"> ×
                                    1</strong>
                                </td>
                                <td className="product-total">
                                    <span className="amount">£50.00</span>
                                </td>
                            </tr>
                            </tbody>
                            <tfoot>
                            <tr className="cart-subtotal">
                                <th>Cart Subtotal</th>
                                <td><span className="amount">£215.00</span></td>
                            </tr>
                            <tr className="shipping">
                                <th>Shipping</th>
                                <td>
                                    <ul>
                                        <li>
                                            <input type="radio"/>
                                            <label>
                                                Flat Rate: <span className="amount">£7.00</span>
                                            </label>
                                        </li>
                                        <li>
                                            <input type="radio"/>
                                            <label>Free Shipping:</label>
                                        </li>
                                        <li/>
                                    </ul>
                                </td>
                            </tr>
                            <tr className="order-total">
                                <th>Order Total</th>
                                <td><strong><span className="amount">£215.00</span></strong>
                                </td>
                            </tr>
                            </tfoot>
                        </table>
                    </div>
                    <div className="payment-method">
                        <div className="payment-accordion">
                            <h5>Direct Bank Transfer</h5>
                            <div className="payment-content">
                                <p>Make your payment directly into our bank account. Please use your
                                    Order ID as the payment reference. Your order won’t be shipped
                                    until the funds have cleared in our account.</p>
                            </div>

                            <h5>Cheque Payment</h5>
                            <div className="payment-content">
                                <p>Please send your cheque to Store Name, Store Street, Store Town,
                                    Store State / County, Store Postcode.</p>
                            </div>

                            <h5>PayPal</h5>
                            <div className="payment-content">
                                <p>Pay via PayPal; you can pay with your credit card if you don’t
                                    have a PayPal account.</p>
                            </div>

                        </div>
                        <div className="order-button-payment">
                            <input type="submit" />
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default YourOrderCom;