import React, {useContext} from 'react';
import {InternetContext} from "../../context";
import "./filterleft.scss"
import SearchBarCom from "../SearchBarCom/SearchBarCom";

const FilterFoodCom = () => {
    const {brand, handlerFilterSalads, filterItems} = useContext(InternetContext)
    return (
        <div className="col-lg-3 order-lg-1 order-2">
            <div className="shop-sidebar-wrap">
                <div className="shop-sidebar mb-30">
                    <h4 className="title">IZLASH</h4>
                    <SearchBarCom/>
                    {/*<FilterLeftCom/>*/}
                </div>

                <div className="shop-sidebar mb-30">
                    <h4 className="title">KATEGORIYALAR</h4>
                    <ul>
                        {brand.map((category, index) => {
                            return (
                                <li key={category.id}>
                                    {/* eslint-disable-next-line jsx-a11y/anchor-is-valid */}
                                    <a href="#" key={index} onClick={() => filterItems(category)}
                                       data-toggle="tab"
                                       aria-expanded="true">
                                        {category}
                                    </a>
                                </li>
                            );
                        })}
                    </ul>
                </div>

                <div className="shop-sidebar mb-30">
                    <h4 className="title">RANGLAR</h4>
                    <ul>
                        {/* eslint-disable-next-line jsx-a11y/anchor-is-valid */}
                        <li><a>
                            <input className="form-check-input me-2" type="checkbox" id="checkboxNoLabel" value="qora"
                                   aria-label="..."/>
                            Qora <span>(18)</span></a></li>
                        <li><a>
                            <input className="form-check-input me-2" type="checkbox" id="checkboxNoLabel" value="oq"
                                   aria-label="..."/>
                            Oq <span>(16)</span></a></li>
                        <li><a>
                            <input className="form-check-input me-2" type="checkbox" id="checkboxNoLabel" value="ko'k"
                                   aria-label="..."/>
                            Ko'k <span>(6)</span></a></li>
                        <li><a>
                            <input className="form-check-input me-2" type="checkbox" id="checkboxNoLabel" value="sariq"
                                   aria-label="..."/>
                            Sariq <span>(11)</span></a></li>
                        <li><a>
                            <input className="form-check-input me-2" type="checkbox" id="checkboxNoLabel" value="qizil"
                                   aria-label="..."/>
                            Qizil <span>(11)</span></a></li>
                    </ul>
                    <h4 className="title">RAZMERLARI</h4>
                    <ul onChange={handlerFilterSalads}>
                        {/* eslint-disable-next-line jsx-a11y/anchor-is-valid */}
                        <li><a>
                            <input className="form-check-input me-2" type="checkbox" id="checkboxNoLabel" value="50x50"
                                   aria-label="..."/>
                            50x50 <span>(11)</span></a></li>
                        {/* eslint-disable-next-line jsx-a11y/anchor-is-valid */}
                        <li><a>
                            <input className="form-check-input me-2" type="checkbox" id="checkboxNoLabel" value="60x60"
                                   aria-label="..."/>
                            60x60 <span>(13)</span></a></li>
                        {/* eslint-disable-next-line jsx-a11y/anchor-is-valid */}
                        <li><a>
                            <input className="form-check-input me-2" type="checkbox" id="checkboxNoLabel" value="70x70"
                                   aria-label="..."/>
                            70x70 <span>(6)</span></a></li>
                        {/* eslint-disable-next-line jsx-a11y/anchor-is-valid */}
                        <li><a>
                            <input className="form-check-input me-2" type="checkbox" id="checkboxNoLabel" value="80x80"
                                   aria-label="..."/>
                            80x80 <span>(51)</span></a></li>
                        {/* eslint-disable-next-line jsx-a11y/anchor-is-valid */}
                        <li><a><input className="form-check-input me-2" type="checkbox" id="checkboxNoLabel"
                                      value="90x90" aria-label="..."/>
                            90x90 <span>(3)</span></a></li>
                    </ul>

                </div>
            </div>
        </div>
    );
};

export default FilterFoodCom;