import React, {useContext, useState} from 'react';
import {InternetContext} from "../../context";

const SearchBarCom = () => {
    const {setDataItems, dataItems} = useContext(InternetContext)

    const [enteredValue, setEnteredValue] = useState("")
    const filterByName = (e) =>  {
        setEnteredValue(e.target.value)
        const filteredData = dataItems.filter(el => {
            return el.name.includes(enteredValue)
        })
        console.log()
        setDataItems(filteredData)

    }


    return (
        <div>
            <div className="form-group d-flex mb-3">
                <input name="s"
                       onChange={(e)=> filterByName(e) }
                       value={enteredValue}
                       className="form-control"
                       placeholder="Qidirish" type="text"/>
                <button type="submit" value="submit" className="btn btn-primary"><i
                    className="icofont icofont-search fas fa-search"/></button>
            </div>
        </div>
    );
};

export default SearchBarCom;