import React, {useContext} from 'react';
import {Link} from "react-router-dom";
// import styled from "styled-components";
import {InternetContext} from "../../context";
import {Icon} from '@iconify/react';
import "./praduktlist.scss"
import styled from "styled-components";


const ShopListCom = ({el}) => {
    const {addTocart} = useContext(InternetContext)

    return (
        <Div className="row product-layout-list">
            <div className="col-lg-4 col-md-5">
                <div className="single-product-wrap">
                    <div className="product-image">
                        {/* eslint-disable-next-line jsx-a11y/anchor-is-valid */}
                        <Link className="imgBox" to={`/shop/${el.id}`}><img src={el.img1} alt="Produce Images"/></Link>
                        {/*<span className="label">30% Off</span>*/}
                        <div className="product-action">
                            {/* eslint-disable-next-line jsx-a11y/anchor-is-valid */}
                            <a onClick={() => addTocart(el)} className="add-to-cart">
                                <Icon icon="ion:bag-handle-outline"/></a>
                            {/* eslint-disable-next-line jsx-a11y/anchor-is-valid */}
                            <a href="#" className="wishlist">
                                <Icon icon="zmdi:favorite-outline"/></a>
                            {/* eslint-disable-next-line jsx-a11y/anchor-is-valid */}
                            <a href="#" className="quick-view" data-toggle="modal" data-target="#exampleModalCenter">
                                <Icon icon="bx:bx-search-alt"/></a>
                        </div>
                    </div>
                </div>
            </div>
            <div className="col-lg-8 col-md-7">
                <div className="product-content text-left">
                    <h3><Link to={`/shop/${el.id}`}>{el.name}</Link></h3>
                    <div className="price-box">
                        {/*<span className="old-price">$56</span>*/}
                        <span className="new-price">{el.price}</span>
                    </div>
                    <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit.
                        Veritatis pariatur ipsa sint consectetur velit maiores
                        sit voluptates aut tempora totam, consequatur iste quod
                        suscipit natus. Explicabo facere neque adipisci
                        odio.</p>
                </div>
            </div>
        </Div>
    );
};
const Div = styled.div`
  .product-image{
    width: 100%;
    height: 350px!important;
  }
  .imgBox{
    margin-left: 95px;
  }
  img{
    height: 340px;
  }
  .single-product-wrap img{
    width: 100%;
  }
  .product-content h3 {
    font-size: 16px;
    font-weight: 500;
    margin: 0;
  }

  .product-content h3 a {
    color: inherit;
    display: inline-block;
    line-height: inherit;
    text-decoration: none;
    cursor: pointer;
    margin: 10px;
    text-align: start;
  }

`

export default ShopListCom;