import React, {useContext} from 'react';
import {Nav} from "react-bootstrap";
import {Link} from "react-router-dom";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faShoppingBag} from "@fortawesome/free-solid-svg-icons";
import {InternetContext} from "../../context";
import {Icon} from "@iconify/react";
import "./Savat.scss"
import styled from "styled-components";

const CorzinkaCom = () => {
    const {cardArray, RemoveCard} = useContext(InternetContext)

    return (
        <Nav.Link className="img_logo" href="#memes">
            <Div className="shopping-cart-wrap">

                <FontAwesomeIcon icon={faShoppingBag} className="icofont icofont-cart-alt img_logo"/>
                <sup className="sup">{cardArray.length}</sup>

                <ul className="mini-cart">
                    {cardArray.map(el =>{
                        return(
                            <li key={el.id} className="cart-item">
                                <div className="cart-image">
                                    <a href=""><img alt="" src={el.img1}/></a>
                                </div>
                                <div className="cart-title">
                                    <Link to={`/shop/${el.id}`}>
                                        <h4>{el.name}</h4>
                                    </Link>
                                    <span className="quantity">1 ×</span>
                                    <div className="price-box"><span className="new-price">{el.price}</span></div>
                                    <a className="remove_from_cart" href="#"><Icon  onClick={() => {RemoveCard(el.id)}} icon="akar-icons:trash-can"/></a>
                                </div>
                            </li>
                        )
                    })}

                    <li className="subtotal-titles">
                        <div className="subtotal-titles">
                            <h3>Hamasi :</h3><span>$ 230.99</span>
                        </div>
                    </li>
                    <li className="mini-cart-btns">
                        <div className="cart-btns">
                            <Link to="/cardcom">Savatni ko'rish</Link>
                            <Link to="/checkout">Sotib olish</Link>
                        </div>
                    </li>
                </ul>
            </Div>
        </Nav.Link>
    );
};
const Div = styled.div`

  .sup {
    position: relative;
    width: 20px;
    height: 15px;
    top: -14px;
    left: -25px;
    padding: 5px;
    border-radius: 50%;
    background-color: red;
    color: #FFFFFF;
    text-decoration: none;
  }
`

export default CorzinkaCom;