import React, {createContext, useState, useEffect} from "react";

const urlAll  = "http://81.95.232.205:8007/"

const InternetContext = createContext(undefined);

const ContextProvider = ({children}) => {
    const [dataItems, setDataItems] = useState([])

    const allBrand = ['ALL', ...new Set(dataItems.map((data) => data.brand,))];

    const [brand, setBrand] = useState(allBrand);
    const [cardArray, setCardArray] = useState([])
    const [filter, setFilter] = useState("grid")
    const [datals, setDatals] = useState([])
    const [searchTerm, setSearchTerm] = useState('a')
    const [shopCard, setShopCard] = useState("coment")
    const [dashbord, setDashbord] = useState("1")


    const [page, setPage] = useState(1)
    const [createButton, setCreateButton] = useState([])

    /// Kiyimlar qsimi
    const getCoctail = async () => {
        const response = await fetch(urlAll + "Kiyimlar/")
        const data = await response.json()
        const {results} = data;
        setDataItems(results);
    }
    useEffect(() => {
        getCoctail()
        setDataItems(DisplayList(dataItems, 6, page))
    }, [page]);




    //// Aloqa qsimi
    const [name, setName] = useState("")
    const [email, setEmail] = useState("")
    const [subject, setSubject] = useState("")
    const [number, setNumber] = useState("")
    const sendData = (e) => {console.log('ll');
        e.preventDefault()
        fetch(urlAll + "aloqa/",{
            method: 'POST',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({
                name,
                Email:email,
                subject,
                number,
            })
        }).then((e) => {
            return e.json()
        }).then((data) => {
            console.log(data);
        })
    }



    //// Filter hamasini chiqarish
    const filterItems = (brand) => {
        if (brand === 'ALL') {
            return;
        }
        const newItems = dataItems.filter((item) => item.brand === brand);
        setDataItems(newItems);
    };

    //// ID orqali krish
    const toggleFavorite = (id) => {
        const Favorite = dataItems.map(el => {
            if (el.id === id) {
                return {
                    ...el,
                    isFavorite: !el.isFavorite
                }
            }
            return el
        })
        setDataItems(Favorite);
    }

    const AddCard = (newItem) => {
        setCardArray([...cardArray, newItem])
    }

    //// Korzinkadagi elementni o'chrish
    const RemoveCard = (id) => {
        let newArray = cardArray.filter((el) => {
            return el.id !== id
        })
        setCardArray(newArray)
    }


    //// Data Filter Search
    const handlerFilterCategory = (e) => {
        const category = e.target.value
        const newArray = dataItems.filter(el => {
            return el.brand.toLowerCase().includes(category.toLowerCase())
        })
        setDataItems(newArray)
        if (category === ' ') {

        }
    }
    //// data filter sumasi
    const handlerFilterCos = (e) => {
        const searchBrand = e.target.value
        const nArray = dataItems.filter(el => {
            return el.price == searchBrand
        })
        setDataItems(nArray)
        if (searchBrand === " ") {
        }
    }

    //// data filter Yulduzlar
    const handlerFilter = (e) => {
        const rating = e.target.value
        const nArray = dataItems.filter(el => {
            return el.rating == rating
        })
        setDataItems(nArray)
        if (rating === '') {
        }
    }
    //// data filter input radio
    const handlerFilterDelivery = (e) => {
        const rating = e.target.value
        const nArray = dataItems.filter(el => {
            return el.brand == rating
        })
        setDataItems(nArray)
        if (rating === " ") {

        }
    }
    const handlerFilterSalads = (e) => {
        const salads = e.target.value
        const nArray = dataItems.filter(el => {
            return el.size === salads
        })
        setDataItems(nArray)
        if (salads === '') {

        }
    }


    // addtoCarrt
    function addTocart(newItem) {
        let newObject = cardArray.filter(el => {
            return el.id === newItem.id
        })
        if (newObject.length === 0) {
            setCardArray([...cardArray, newItem])

        }
        console.log(newObject)
    }


    //// Pagination
    const nextPage = (index) => {
        setPage(index + 1)
        setDataItems(DisplayList(dataItems, 6, page))
        console.log(page)
    }

    // pagination
    function DisplayList(array, rows, current_page) {
        current_page--
        // eslint-disable-next-line no-unused-vars
        let html = ``
        let start = current_page * rows;
        let end = start + rows;
        let pagination = array.slice(start, end);
        setCreateButton(pagination)
        return pagination
    }

    return (
        <InternetContext.Provider value={{
            nextPage, createButton,
            dataItems, setDataItems,   addTocart, dashbord, setDashbord,
            handlerFilterSalads, handlerFilterDelivery,
            handlerFilter, handlerFilterCos, handlerFilterCategory,
            shopCard, setShopCard,
            brand, datals, setDatals, searchTerm,
            setSearchTerm, filterItems, setBrand, toggleFavorite,
            AddCard, cardArray, RemoveCard, filter, setFilter,

            name, setName, email, setEmail, subject, setSubject, number, setNumber, sendData


        }}>
            <div>{children}</div>
        </InternetContext.Provider>
    )
}

export {InternetContext, ContextProvider}