import React, {useContext} from 'react';
import {InternetContext} from "../../context";
import { Icon } from '@iconify/react';
import "./filtertop.scss"


const FilterTopCom = () => {
const { setFilter,  handlerFilterCos} = useContext(InternetContext)

    return (
        <div>
            <div className="row">
                <div className="col">
                    <div className="shop-top-bar">

                        <div className="product-mode">
                            <ul className="nav shop-item-filter-list" role="tablist">
                                <li onClick={()=>{
                                    setFilter('grid')
                                }} className="active"><a className="active" data-toggle="tab" href="#grid">
                                    <Icon icon="eva:keypad-outline"  whids="25" height="25" /></a></li>
                                <li  onClick={()=>{
                                    setFilter('list')
                                }}><a data-toggle="tab" href="#list">
                                    <Icon icon="typcn:th-list-outline" whids="25" height="25"  />
                                </a></li>
                            </ul>
                        </div>
                        <div className="product-short">
                            <select onChange={handlerFilterCos} className="nice-select" >
                                <option value=" ">Narxlar</option>
                                <option value="150 000">150 000 UZS</option>
                                <option value="130 000 ">130 000 UZS</option>
                                <option value="100 000">100 000 UZS</option>
                                <option value="90 000">90 000 UZS</option>
                                <option value="85 000">85 000 UZS</option>
                                <option value="75 000">75 000 UZS</option>
                                <option value="65 000">65 000 UZS</option>
                            </select>
                        </div>
                    </div>
                </div>
            </div>
        </div>

    );
};

export default FilterTopCom;